/*
  
  
   middle small large
*/

import {
  ViewContainerRef,
  Component,
  OnInit,
  Input,
  forwardRef,
  AfterViewInit,
  Renderer2,
  ElementRef,
  ViewChild,
  Renderer,
  ViewChildren,
  OnChanges
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { Observable } from 'rxjs/Observable';

@Component({
  selector: 'app-jdb-plg-select',
  template: `<!--  --> <div *ngIf="_jdbMode=='chooseOne'" #inputDom class="jdb-plg-select-one" (click)="dialogShow($event)" [ngClass]="_classMap" [ngStyle]="{'width':_width}"> <!-- placeHolder --> <div class="jdb-plg-select-placeholder" [hidden]="inputText!=''">{{_placeHolder}}</div> <!--  --> <!-- <span class="chooseOne" [hidden]="inputText==''">{{inputText}}</span> --> <input class="chooseOne chooseOneInput" [hidden]="inputText==''" type="text" [(ngModel)]="inputText" readonly> <ul #optionList [ngClass]="{ 'options-show':show, 'options-no-margin':!spaceFlex} " class="options "> <!--  --> <li *ngFor="let option of _selectList " (click)="item($event,option) " [ngClass]="{active:ngModelValue===option[_optionValue],disabled:option[_jdbItemDisabled] === _jdbSureDisabled} "> <img class="img-box" *ngIf="_showImgBox&&option.imgUrl" [src]="option.imgUrl" alt=""> <span class="img-box" *ngIf="_showImgBox&&!option.imgUrl"></span> <span class="text-box">{{_optionText=='option'?option:option[_optionText]}}</span> </li> </ul> <!--  --> <span class="close-icon icon-empty " [hidden]="!isShowClear " (click)="clearInputText($event) "></span> <!--  --> <span class="select-icon icon-select-arrow " [hidden]="isShowClear "></span> </div> <!--  --> <div *ngIf="_jdbMode=='chooseMore' " #inputDom class="jdb-plg-select-more " (click)="dialogShow($event) " [ngClass]="_classMap " [ngStyle]="{ 'width':_width} "> <!-- placeHolder --> <div class="jdb-plg-select-placeholder " [hidden]="inputText.length !=0 ">{{_placeHolder}}</div> <!-- item --> <ul class="chooseMore "> <li *ngFor="let item of inputText "> {{item.text}} <span class="item-delete icon-close " (click)="deleteMoreItem($event,item) "></span> </li> </ul> <ul #optionList [ngClass]="{ 'options-show':show, 'options-no-margin':!spaceFlex} " class="options "> <li class="choose-more " *ngFor="let option of _selectList " (click)="chooseMore($event,option) " [ngClass]="{ 'active':moreIndex(option),disabled:option[_jdbItemDisabled] === _jdbSureDisabled} "> <!-- {{_optionText=='option'?option:option[_optionText]}} --> <img class="img-box" *ngIf="_showImgBox&&option.imgUrl" [src]="option.imgUrl" alt=""> <span class="img-box" *ngIf="_showImgBox&&!option.imgUrl"></span> <span class="text-box">{{_optionText=='option'?option:option[_optionText]}}</span> <span [hidden]="!moreIndex(option) " class="choose-right icon-selected "></span> </li> </ul> <!--  --> <span class="close-icon icon-empty " [hidden]="!isShowClear " (click)="clearInputText($event) "></span> </div> <!--  --> <div *ngIf="_jdbMode=='chooseNum' " #inputDom class="jdb-plg-select-num " (click)="dialogShow($event) " [ngClass]="_classMap " [ngStyle]="{ 'width':_width} "> <!-- placeHolder --> <div class="jdb-plg-select-placeholder " [hidden]="inputText!=0 ">{{_placeHolder}}</div> <span class="choose-tip " [hidden]="inputText==0 ">{{inputText}}</span> <ul #optionList [ngClass]="{ 'options-show':show, 'options-no-margin':!spaceFlex} " class="options "> <li class="choose-more " *ngFor="let option of _selectList " (click)="numClick($event,option) " [ngClass]="{ 'active':moreIndex(option),disabled:option[_jdbItemDisabled] === _jdbSureDisabled} "> <!-- {{_optionText=='option'?option:option[_optionText]}} --> <img class="img-box" *ngIf="_showImgBox&&option.imgUrl" [src]="option.imgUrl" alt=""> <span class="img-box" *ngIf="_showImgBox&&!option.imgUrl"></span> <span class="text-box">{{_optionText=='option'?option:option[_optionText]}}</span> <span [hidden]="!moreIndex(option) " class="choose-right icon-selected "></span> </li> </ul> <!--  --> <span class="close-icon icon-empty " [hidden]="!isShowClear " (click)="clearInputText($event) "></span> <span class="select-icon icon-select-arrow " [hidden]="isShowClear "></span> </div> <!--  --> <div class="jdb-plg-select-master " *ngIf="show "></div>`,
  // styleUrls:  ['./jdb-plg-select.component.scss'],
  providers: [
    {// 
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => JdbPlgSelectComponent),
      multi: true
    }
  ]
})
export class JdbPlgSelectComponent implements OnInit, OnChanges {
  _selectList: any;
  _size = 'middle';
  _width: string;
  _optionText = 'text';  // 
  _optionValue = 'value'; // 
  _optionPosition: string;
  isShowClear = false; // x
  _jdbClear = false;
  _jdbDisabled = false; // 
  _jdbMode = 'chooseOne';
  _placeHolder = '';
  _chooseMoreArray = []; // 
  _classMap = {};
  listHeight: number;
  savaHeight = true;
  spaceFlex = true;  // 
  _showImgBox = false; // 
  _jdbItemDisabled = 'disabled'; // disabled
  _jdbSureDisabled = 2; // 1 2
  _jdbNoDisabled = 1; // 2

  // 
  @Input() jdbClassName = '';

  // 
  @Input()
  set jdbItemDisabled(value) {
    this._jdbItemDisabled = value;
  }
  get jdbItemDisabled(): string {
    return this._jdbItemDisabled;
  }

  // 
  @Input()
  set jdbSureDisabled(value) {
    this._jdbSureDisabled = value;
  }
  get jdbSureDisabled(): any {
    return this._jdbSureDisabled;
  }

  // // 
  // @Input()
  // set jdbNoDisabled(value) {
  //   this._jdbNoDisabled = value;
  // }
  // get jdbNoDisabled(): any {
  //   return this._jdbNoDisabled;
  // }

  // 
  @Input()
  set jdbPlaceHolder(value) {
    this._placeHolder = value;
  }
  get jdbPlaceHolder(): any {
    return this._placeHolder;
  }

  // 
  @Input()
  set jdbClear(value) {
    this._jdbClear = this.toBoolean(value);
  }
  get jdbClear(): boolean {
    return this._jdbClear;
  }

  // 
  @Input()
  set jdbSelectList(value) {
    this._selectList = value;

    // 
    if (this._selectList) {
      this._selectList.forEach(element => {
        if (element.imgUrl) {
          this._showImgBox = true;
        }
      });
    }
  }
  get jdbSelectList(): string {
    return this._selectList;
  }

  // 30pxsmall24px,large40px;
  @Input()
  set jdbSize(value) {
    this._size = value;
  }
  get jdbSize(): string {
    return this._size;
  }

  // 
  @Input()
  set jdbWidth(value) {
    this._width = value;
  }
  get jdbWidth(): string {
    return this._width;
  }

  // 
  @Input()
  set jdbOptionText(value) {
    this._optionText = value;
  }
  get jdbOptionText(): string {
    return this._optionText;
  }

  // serve
  @Input()
  set jdbOptionValue(value) {
    this._optionValue = value;
  }
  get jdbOptionValue(): string {
    return this._optionValue;
  }

  // 
  @Input()
  set jdbDisabled(value) {
    this._jdbDisabled = this.toBoolean(value);
  }
  get jdbDisabled(): boolean {
    return this._jdbDisabled;
  }

  // selectchooseMore
  @Input()
  set jdbMode(value) {
    this._jdbMode = value;
  }
  get jdbMode(): string {
    return this._jdbMode;
  }

  @ViewChild('inputDom') inputDom: ElementRef;
  @ViewChild('optionList') optionList: ElementRef;

  show = false;
  inputText: any;
  ngModelValue = '';
  constructor(private renderer2: Renderer2, private renderer: Renderer) {
  }

  ngOnInit() {
    

  }

  // tslint:disable-next-line:use-life-cycle-interface
  ngAfterViewInit() {
    // 
    this.renderer2.listen('document', 'click', () => {
      this.show = false;
      this.renderer.setElementClass(this.inputDom.nativeElement, 'jdb-plg-select-active', this.show);
    });

    if (this._jdbClear && !this._jdbDisabled) {
      // x
      this.renderer2.listen(this.inputDom.nativeElement, 'mouseenter', () => {
        // 

        if (this._jdbMode === 'chooseOne' || this._jdbMode === 'chooseNum') {
          if (!this.inputText || this.show) {
            return;
          }
        } else if (this._jdbMode === 'chooseMore') {
          if (this.inputText.length === 0 || this.show) {
            return;
          }
        }

        this.isShowClear = true;
        this.renderer.setElementClass(this.inputDom.nativeElement, 'jdb-plg-select-active', this.show);
      });
      this.renderer2.listen(this.inputDom.nativeElement, 'mouseleave', () => {
        // 
        if (this._jdbMode === 'chooseOne' || this._jdbMode === 'chooseNum') {
          if (!this.inputText || this.show) {
            return;
          }
        } else if (this._jdbMode === 'chooseMore') {
          if (this.inputText.length === 0 || this.show) {
            return;
          }
        }

        this.isShowClear = false;
        this.renderer.setElementClass(this.inputDom.nativeElement, 'jdb-plg-select-active', this.show);
      });
    }

  }

  ngOnChanges() {
    if (this._jdbMode === 'chooseOne') {
      this.inputText = '';
    } else if (this._jdbMode === 'chooseMore') {
      this.inputText = [];
    } else if (this._jdbMode === 'chooseNum') {
      this.inputText = 0;
    }
    this.setClassMap();
  }

  setClassMap() {
    if (this._jdbMode === 'chooseMore') {
      this._classMap = {
        [`${this._size}`]: true,
        [`jdb-plg-select-bottom-${this._size}`]: this.inputText.length !== 0,
        ['jdb-plg-select-disabled']: this._jdbDisabled,
        [this.jdbClassName]: true
      };
    } else {
      this._classMap = {
        [`${this._size}`]: true,
        ['jdb-plg-select-disabled']: this._jdbDisabled,
        [this.jdbClassName]: true
      };
    }
  }

  // x
  clearInputText(e) {
    e.stopPropagation();

    if (this._jdbMode === 'chooseOne') {
      this.inputText = '';
    } else if (this._jdbMode === 'chooseMore') {
      this.inputText = [];
      this._chooseMoreArray = [];
    } else if (this._jdbMode === 'chooseNum') {
      this.inputText = 0;
      this._chooseMoreArray = [];
    }
    this.isShowClear = !this.isShowClear;

    // 
    this.ngModelValue = '';
    this.onChange('');

    this.setClassMap();
  }

  // 
  dialogShow(e) {
    e.stopPropagation();
    // 
    if (this._jdbDisabled) {
      return;
    }
    this.isShowClear = false;
    this.show = !this.show;
    this.renderer.setElementClass(this.inputDom.nativeElement, 'jdb-plg-select-active', this.show);
    this.optionPosition(this.optionList.nativeElement.clientHeight);
  }

  // 
  optionPosition(listHeight) {
    const offetTop = this.getTop(this.inputDom.nativeElement);  // offetTop
    const scrollTop = this.getScrollTop(this.inputDom.nativeElement.parentElement);
    const clientHeight = document.documentElement.clientHeight || document.body.clientHeight; // 
    const elemHeight = this.inputDom.nativeElement.clientHeight; // 
    let paddingHeight;
    if (this.jdbSize === 'small') {
      paddingHeight = 2;
    } else if (this.jdbSize === 'large') {
      paddingHeight = 9;
    } else if (this.jdbSize === 'middle') {
      paddingHeight = 5;
    }
    const flexHeight = clientHeight - offetTop - elemHeight - paddingHeight + scrollTop; // 
    if (flexHeight < listHeight) {
      // 
      this.spaceFlex = false;
      this.renderer.setElementStyle(this.optionList.nativeElement, 'transform-origin', '100% 100%');
      if (listHeight < 188) {
        this.renderer.setElementStyle(this.optionList.nativeElement, 'top', - listHeight - 5 + 'px');
      } else {
        this.renderer.setElementStyle(this.optionList.nativeElement, 'top', -190 - paddingHeight + 'px');
      }
    } else {
      this.spaceFlex = true;
      this.renderer.setElementStyle(this.optionList.nativeElement, 'top', '');
      this.renderer.setElementStyle(this.optionList.nativeElement, 'transform-origin', '0% 0%');
    }
  }

  // ControlValueAccessor  ngModel
  writeValue(value: any): void {
    this.ngModelValue = value;

    // 
    // if (this._jdbMode === 'chooseOne') {
    //   this.forOneStart(value);
    // } else if (this._jdbMode === 'chooseMore') {
    //   this.forMoreStart(value);
    //   this.setClassMap();
    // } else if (this._jdbMode === 'chooseNum') {
    //   this.forNumStart(value);
    // }
    if (value === null || value === '' || value === undefined) {
      // null
      if (this._jdbMode === 'chooseMore') {
        this.inputText = [];
      } else {
        this.inputText = '';
      }
    } else {
      if (this._jdbMode === 'chooseOne') {
        this.forOneStart(value);
      } else if (this._jdbMode === 'chooseMore') {
        this.forMoreStart(value);
        this.setClassMap();
      } else if (this._jdbMode === 'chooseNum') {
        this.forNumStart(value);
      }
    }
  }
  onChange: (value: any) => void = () => null;

  registerOnChange(fn: (value: any) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
  }

  setDisabledState(isDisabled: boolean): void {
  }

  // 
  forOneStart(value) {
    this._selectList.forEach(elem => {
      if (elem[this._optionValue] === value) {
        this.inputText = elem[this._optionText];
      }
    });
  }

  // 
  forMoreStart(value) {
    value = value.split(',');

    value.forEach(item => {
      this._selectList.forEach(elem => {
        if (elem[this._optionValue] === item) {
          // inputText
          const text = this._optionText;
          const value = this._optionValue;
          this.inputText.push({
            text: elem[this._optionText],
            value: elem[this._optionValue]
          });

          // this._chooseMoreArray
          this._chooseMoreArray.push(elem[this._optionValue]);
          return;
        }
      });
    });
  }

  // 
  forNumStart(value) {
    value = value.split(',');
    value.forEach(item => {
      this._selectList.forEach(elem => {
        if (elem[this._optionValue] === item) {
          this.inputText++;
          this._chooseMoreArray.push(elem[this._optionValue]);
          return;
        }
      });
    });
  }

  // 
  item(e, item) {
    // 
    e.stopPropagation();

    // showtrue
    if (!this.show) {
      return;
    }
    // 
    if (item[this._jdbItemDisabled] === this._jdbSureDisabled) {
      return;
    }

    this.inputText = item[this._optionText];
    this.show = !this.show;
    this.renderer.setElementClass(this.inputDom.nativeElement, 'jdb-plg-select-active', this.show);

    this.ngModelValue = item[this._optionValue];
    this.onChange(item[this._optionValue]);
  }

  // 
  chooseMore(e, item) {
    let flag = false;
    // 
    e.stopPropagation();

    // showtrue
    if (!this.show) {
      return;
    }

    // 
    if (item[this._jdbItemDisabled] === this._jdbSureDisabled) {
      return;
    }

    // 
    this.inputText.forEach((element, index) => {
      if (element[this._optionValue] === item[this._optionValue]) {
        flag = true;
        return;
      }
    });

    if (flag) {
      this.deleteMoreItem(e, item);
      return;
    }

    // inputText
    const text = this._optionText;
    const value = this._optionValue;
    this.inputText.push({
      text: item[this._optionText],
      value: item[this._optionValue]
    });

    // this._chooseMoreArray
    this._chooseMoreArray.push(item[this._optionValue]);
    this.ngModelValue = this._chooseMoreArray.toString();
    this.onChange(this._chooseMoreArray);
    this.show = true;
    this.setClassMap();
  }

  // li
  numClick(e, item) {
    let flag = false;
    // 
    e.stopPropagation();

    // showtrue
    if (!this.show) {
      return;
    }

    // 
    if (item[this._jdbItemDisabled] === this._jdbSureDisabled) {
      return;
    }

    // 
    this._chooseMoreArray.forEach((element, index) => {
      if (element === item[this._optionValue]) {
        flag = true;
        this._chooseMoreArray.splice(index, 1);
        return;
      }
    });
    if (flag) {
      this.inputText--;
      return;
    }

    this.inputText++;
    this.show = true;
    this._chooseMoreArray.push(item[this._optionValue]);
    this.ngModelValue = this._chooseMoreArray.toString();
    this.onChange(this._chooseMoreArray);
  }

  // inputText
  moreIndex(item) {
    let flag = false;
    this._chooseMoreArray.forEach((element, index) => {
      if (element === item[this._optionValue]) {
        flag = true;
        return;
      }
    });
    return flag;
  }

  // 
  deleteMoreItem(e, item) {
    e.stopPropagation();
    if (this._jdbDisabled) {
      return;
    }

    this.inputText.forEach((element, index) => {
      if (element[this._optionValue] === item[this._optionValue]) {
        this.inputText.splice(index, 1);
        return;
      }
    });

    this._chooseMoreArray.forEach((element, index) => {
      if (element === item[this._optionValue]) {
        this._chooseMoreArray.splice(index, 1);
        return;
      }
    });
    this.ngModelValue = this._chooseMoreArray.toString();
    this.onChange(this._chooseMoreArray);
    this.setClassMap();
  }

  // boolean,true,false
  toBoolean(value: boolean | string): boolean {
    return value === '' || (value && value !== 'false');
  }

  // offetTop
  getTop(e) {
    let offset = e.offsetTop;
    if (e.offsetParent != null) {
      //translateY
      if (e.style.transform) {
        let ret = this.parseTranslateY(e.style.transform);
        offset += ret.isPercent ? e.clientHeight * ret.translateY / 100 : ret.translateY;
      }
      offset += this.getTop(e.offsetParent);
    }
    return offset;
  }

  // scrollTop
  getScrollTop(e) {
    let offset = e.scrollTop;
    if (e.parentElement != null) {
      offset += this.getScrollTop(e.parentElement);
    }
    return offset;
  }

  //translateY
  parseTranslateY(val) {
    let reg = /\(([^()]+)\)/g;
    let translate = reg.exec(val)[1];
    let translatArr = translate.split(',');
    let translateY;
    let isPercent;
    //translate
    if (val.indexOf('translate') === -1) {
      return {
        isPercent: false,
        translateY: 0
      }
    }
    //translatetranslateY
    if (translatArr.length === 2) {
      translateY = translate.split(',')[1];
    } else if (translatArr.length === 1 && val.indexOf('translateY') !== -1) {
      translateY = translate;
    }
    //px
    if (translateY.indexOf('px') !== -1) {
      //px
      isPercent = false;
      translateY = Number(translateY.slice(0, -2));
    } else if (translateY.indexOf('%') !== -1) {
      isPercent = true;
      translateY = Number(translateY.slice(0, -1));
    }
    //number
    return {
      isPercent,
      translateY
    };
  }
}
