
import {
  Component,
  OnInit,
  EventEmitter,
  Input,
  Output,
  ElementRef,
  ViewChild,
  Renderer2
} from '@angular/core';

@Component({
  selector: 'app-jdb-plg-pagination',
  template: `<div class="jdb-plg-pagination"> <!--  --> <span *ngIf="_showTotal" class="total-box"> {{_total}} </span> <div class="operate-box"> <!--  --> <div class="jdb-plg-pagination-options" *ngIf="_showPageSize"> <app-jdb-plg-select (ngModelChange)="dataChange(false,$event)" [jdbSize]="'small'" [jdbWidth]="'90px'" [(ngModel)]="_pageSize" [jdbSelectList]="_options"></app-jdb-plg-select> </div> <!--  --> <ul *ngIf="!_jdbSimple" class="base-pagination"> <!--  --> <li class="jdb-plg-pagination-prev" title="" [ngClass]="{'disabled':_current===_firstIndex}" (click)="dataChange(true,_current-1)"> <span class="jdbIcon icon-pagination-prev"></span> </li> <!--  --> <li class="jdb-plg-pagination-first" title="" [ngClass]="{'active':_current===_firstIndex}" (click)="dataChange(true,_firstIndex)"> {{_firstIndex}} </li> <!--  --> <li class="jdb-plg-pagination-forward" *ngIf="(_lastIndex >9)&&(_current-4>_firstIndex)" (click)="jumpBefore(_pageSize)"> <span class="icon-pagination-more"></span> <span class="icon-pagination-jump-prev"></span> </li> <!--  --> <li class="jdb-plg-pagination-pager" *ngFor="let page of pages" [ngClass]="{'active':_current===page.index}" (click)="dataChange(true,page.index)"> {{page.index}} </li> <!--  --> <li class="jdb-plg-pagination-backward" *ngIf="(_lastIndex >9)&&(_current+4<_lastIndex)" (click)="jumpAfter(_pageSize)"> <span class="icon-pagination-more"></span> <span class="icon-pagination-jump-next"></span> </li> <!--  --> <li class="jdb-plg-pagination-last" *ngIf="(_lastIndex>0)&&(_lastIndex!==_firstIndex)" title="" [ngClass]="{'active':_current===_lastIndex}" (click)="dataChange(true,_lastIndex)"> {{_lastIndex}} </li> <!--  --> <li class="jdb-plg-pagination-next" title="" [ngClass]="{'disabled':_current===_lastIndex}" (click)="dataChange(true,_current+1)"> <span class="jdbIcon icon-pagination-next"></span> </li> </ul> <!--  --> <div class="simple-pagination" *ngIf="_jdbSimple"> <div class="left-box"> <span class="icon-pagination-first" [ngClass]="{'disabled':_current===_firstIndex}" (click)="dataChange(true,_firstIndex)"></span> <span class="icon-pagination-prev" [ngClass]="{'disabled':_current===_firstIndex}" (click)="dataChange(true,_current-1)"></span> </div> <div class="center-box"> {{_current}} / {{_lastIndex}} </div> <div class="right-box"> <span class="icon-pagination-next" [ngClass]="{'disabled':_current===_lastIndex}" (click)="dataChange(true,_current+1)"></span> <span class="icon-pagination-last" [ngClass]="{'disabled':_current===_lastIndex}" (click)="dataChange(true,_lastIndex)"></span> </div> </div> <!--  --> <div *ngIf="_showQuickJump" class="quick-jumper">  <input #inputJump type="text" [(ngModel)]="quickJumpPage" (keyup.enter)="quickJump()" appOnlyNumber="true">  <button (click)="quickJump()"></button> </div> </div> </div>`,
  // styleUrls:  ['./jdb-plg-pagination.component.scss']
})
export class JdbPlgPaginationComponent {
  _total: number; // 
  _current = 1; // 1
  _pageSize = 10; // 
  _firstIndex = 1;  // 1
  _lastIndex = Infinity;  // 
  _showTotal = false;  // 
  _showPageSize = false; // 
  _showQuickJump = false; // 
  pages = [];  // 
  // _options = [10, 20, 30, 40, 50]; // select
  // select
  _options = [
    { value: 10, text: '10/' },
    { value: 20, text: '20/' },
    { value: 30, text: '30/' },
    { value: 40, text: '40/' },
    { value: 50, text: '50/' }
  ];

  quickJumpPage: any; // 
  hisQicukPage: any;  // 
  _jdbSimple = false; // 

  @Output() jdbPageSizeChange: EventEmitter<number> = new EventEmitter();  //   
  @Output() jdbPageIndexChange: EventEmitter<number> = new EventEmitter();  // 

  @ViewChild('inputJump') private inputJump: ElementRef;
  constructor(
    private el: ElementRef,
    private renderer2: Renderer2
  ) { }

  // 
  @Input()
  set jdbShowTotal(value: boolean) {
    this._showTotal = this.toBoolean(value);
  }

  get jdbShowTotal(): boolean {
    return this._showTotal;
  }

  // 
  @Input()
  set jdbTotal(value: number) {
    // total
    if (value === this._total) {
      return;
    }
    this._total = value;
    this.setPageNo();
  }

  get jdbTotal(): number {
    return this._total;
  }

  // jdbPageIndex_current
  @Input()
  set jdbPageIndex(value: number) {
    if (this._current === value) {
      return;
    }
    if (value > this._lastIndex || value < this._firstIndex) {
      return;
    }
    this._current = Number(value);
    this.setPageNo();
  }

  get jdbPageIndex(): number {
    return this._current;
  }

  // select
  @Input()
  set jdbShowPageSize(value: boolean) {
    this._showPageSize = this.toBoolean(value);
  }

  get jdbShowPageSize(): boolean {
    return this._showPageSize;
  }

  // 
  @Input()
  set jdbPageSize(value: number) {
    if (value === this._pageSize) {
      return;
    }
    this._pageSize = value;
    this.setPageNo();
  }

  get jdbPageSize(): number {
    return this._pageSize;
  }

  // 
  @Input()
  set jdbSizeOptions(value) {
    // total
    if (value === this._options) {
      return;
    }

    // 
    if (Object.prototype.toString.call(value) === '[object Array]') {
      const optionsArr = [];
      value.forEach(elem => {
        const obj = {
          value: elem,
          text: elem + '/'
        };
        optionsArr.push(obj);
      });
      this._options = optionsArr;
    }
  }

  get jdbSizeOptions() {
    return this._options;
  }

  // 
  @Input()
  set jdbShowQuickJump(value: boolean) {
    this._showQuickJump = this.toBoolean(value);
  }

  get jdbShowQuickJump(): boolean {
    return this._showQuickJump;
  }

  // 
  @Input()
  set jdbSimple(value: boolean) {
    this._jdbSimple = this.toBoolean(value);
  }

  get jdbSimple(): boolean {
    return this.jdbSimple;
  }

  // 
  setPageNo() {
    // 
    this._lastIndex = Math.ceil(this._total / this._pageSize);
    // 
    // if (this._current > this._lastIndex) {
    //   this.jdbPageIndex = this._lastIndex;
    //   this.jdbPageIndexChange.emit(this.jdbPageIndex);
    // }

    const tmpPages = [];

    if (this._lastIndex <= 9) {
      // 9
      for (let i = 2; i <= this._lastIndex - 1; i++) {
        tmpPages.push({
          index: i
        });
      }
    } else {
      const current = +this._current;
      let left = Math.max(2, current - 2);
      let right = Math.min(current + 2, this._lastIndex - 1);

      // 
      if (current === 5) {
        left = 2;
      } else if (current === this._lastIndex - 4) {
        right = this._lastIndex - 1;
      }

      if (current - 1 <= 3) {
        right = 7;
      }

      if (this._lastIndex - current <= 3) {
        left = this._lastIndex - 6;
      }

      for (let i = left; i <= right; i++) {
        tmpPages.push({ index: i });
      }
    }

    this.pages = tmpPages;
  }

  // statustruenumfalsenum
  dataChange(status: boolean, num: number): void {
    if (status) {
      if (num === this._firstIndex - 1 || num === this._lastIndex + 1) {
        return;
      }
      // 
      this.quickJumpPage = '';
      this.jdbPageIndex = num;
      this.jdbPageIndexChange.emit(this.jdbPageIndex);
    } else {
      // 
      this.quickJumpPage = '';
      this.jdbPageSize = num;
      this.jdbPageSizeChange.emit(num);

      // 1
      this.jdbPageIndex = 1;
      this.jdbPageIndexChange.emit(this.jdbPageIndex);
      this.setPageNo();
    }
    // this.setPageNo();
  }

  // 
  quickJump() {
    // 
    if (this.quickJumpPage > this._lastIndex) {
      this.inputJump.nativeElement.focus();
      this.quickJumpPage = '';
      return;
    }

    // 
    if (!this.quickJumpPage) {
      return;
    }

    this.jdbPageIndex = this.quickJumpPage;
    this.jdbPageIndexChange.emit(this.jdbPageIndex);
  }

  // (2)
  jumpBefore(pageSize) {
    this.dataChange(true, this._current - Math.round(pageSize / 2));
  }

  // 
  jumpAfter(pageSize) {
    this.dataChange(true, this._current + Math.round(pageSize / 2));
  }

  // boolean,true,false
  toBoolean(value: boolean | string): boolean {
    return value === '' || (value && value !== 'false');
  }

  // 
  isNumber(obj) {
    const reg = /^[0-9]*$/;
    return reg.test(obj);
  }

}

